@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: aliceblue;
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: aliceblue;
  max-width: 85%;
  margin: 0 auto;
}

.won-card {
  background-color: #54e1a3;
}

.lost-card {
  background-color: #a34d5d;
}
.App{text-align:left;padding:40px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name-section .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:20%;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{justify-self:center;-webkit-align-self:center;align-self:center}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-winner{margin-top:10px}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}

.year-selector{text-align:right;list-style-type:disc}.year-selector li{margin-top:10px;margin-bottom:10px}

.HomePage .app-name{font-size:3rem;margin:30px auto}.HomePage .teams-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.TeamTile{height:150px;text-align:center;border:1px solid snow;padding:20px}

